import { useRef, useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

const Navbar = (props) => {
  //Refs
  const homeRef = props.homeRef;
  const aboutRef = props.aboutRef;
  const newsRef = props.newsRef;
  const activityRef = props.activityRef;
  const contactRef = props.contactRef;
  const neighborhoodRef = props.neighborhoodRef;
  const trafficRef = props.trafficRef;
  const menuCollapseRef = useRef('');
  // 如果 Scroll Y 軸 > 5 為 true；反之則為 false
  const [isScrolled, setIsScrolled] = useState(false);
  const [navShadow, setNavShadow] = useState('');

  const listenScrollEvent = () => {
    if (window.scrollY > 5) {
      setIsScrolled(true);
      setNavShadow('shadow-xl');
    } else {
      setIsScrolled(false);
      setNavShadow('');
    }
  };

  const menuHandler = () => {
    if (!menuCollapseRef.current.classList.contains('hidden')) {
      menuCollapseRef.current.classList.add('hidden');
    } else {
      menuCollapseRef.current.classList.remove('hidden');
    }
  };

  const scrollToHandle = (ref) => {
    window.scrollTo({
      top: ref.current.offsetTop,
      behavior: 'smooth',
    });
  };

  // const scrollToHomeHandle = () => {
  //   window.scrollTo({
  //     top: homeRef.current.offsetTop,
  //     behavior: 'smooth',
  //   });
  // };

  useEffect(() => {
    window.addEventListener('scroll', listenScrollEvent);
  }, [isScrolled]);

  return (
    <div ref={homeRef}>
      <nav
        className={`navbar fixed top-0 z-50 w-full text-center transition duration-500 ease-in-out ${navShadow} ${
          isScrolled ? 'lg:bg-sky-800' : 'lg:bg-transparent'
        }  select-none justify-center bg-sky-800 lg:flex lg:items-stretch`}
      >
        <div className=' mx-9 flex h-12 text-3xl text-black '>
          <button
            onClick={menuHandler}
            className='relative ml-auto block h-12 w-12 cursor-pointer p-4 lg:hidden'
          >
            <svg
              className='fill-current text-white'
              xmlns='http://www.w3.org/2000/svg'
              viewBox='0 0 20 20'
            >
              <path d='M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z' />
            </svg>
          </button>
        </div>
        <div className='lg:flex-no-shrink lg:flex lg:items-stretch'>
          <div className='ml-auto hidden lg:flex' ref={menuCollapseRef}>
            <Link
              onClick={() => scrollToHandle(homeRef)}
              to='/guanxin-algal-reef'
              className=' flex-no-grow flex-no-shrink relative flex items-center py-2 px-4 leading-normal text-white no-underline transition-all ease-out hover:scale-110  hover:text-amber-300 '
            >
              首頁
            </Link>

            <Link
              onClick={() => scrollToHandle(aboutRef)}
              to='#'
              className='flex-no-grow flex-no-shrink ease relative flex items-center py-2 px-4 leading-normal text-white no-underline transition-all hover:scale-110  hover:text-amber-300'
            >
              認識我們
            </Link>
            <Link
              onClick={() => scrollToHandle(newsRef)}
              to='#'
              className='flex-no-grow flex-no-shrink ease relative flex items-center py-2 px-4 leading-normal text-white no-underline transition-all hover:scale-110  hover:text-amber-300'
            >
              最新消息
            </Link>
            <Link
              onClick={() => scrollToHandle(activityRef)}
              to='#'
              className='flex-no-grow flex-no-shrink ease relative flex items-center py-2 px-4 leading-normal text-white no-underline transition-all hover:scale-110  hover:text-amber-300'
            >
              活動體驗
            </Link>
            <Link
              onClick={() => scrollToHandle(neighborhoodRef)}
              to='#'
              className='flex-no-grow flex-no-shrink ease relative flex items-center py-2 px-4 leading-normal text-white no-underline transition-all hover:scale-110  hover:text-amber-300'
            >
              附近景點
            </Link>
            <Link
              to='#'
              className='flex-no-grow flex-no-shrink ease relative flex items-center py-2 px-4 leading-normal text-white no-underline transition-all hover:scale-110  hover:text-amber-300'
            >
              特色產品
            </Link>
            <Link
              onClick={() => scrollToHandle(trafficRef)}
              to='#'
              className='flex-no-grow flex-no-shrink ease relative flex items-center py-2 px-4 leading-normal text-white no-underline transition-all hover:scale-110  hover:text-amber-300'
            >
              交通指南
            </Link>
            <Link
              onClick={() => scrollToHandle(contactRef)}
              to='#'
              className='flex-no-grow flex-no-shrink ease relative flex items-center py-2 px-4 leading-normal text-white no-underline transition-all hover:scale-110  hover:text-amber-300'
            >
              聯絡我們
            </Link>
          </div>
        </div>
      </nav>
    </div>
  );
};

export default Navbar;
